<template>
  <el-dialog
    :visible="is_show"
    title="创建标签"
    width="30%"
    top="25vh"
    center>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="标签名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="cancel()">取 消</el-button>
    <el-button type="primary" @click="submit()">确 定</el-button>
  </span>
  </el-dialog>
</template>

<script>
  import {createTag, editTag} from './service'
  export default {
    data () {
      return {
        form: {},
        is_show: true
      }
    },
    mounted () {
      if (this.params.name) {
        this.form = this.params
      }
    },
    methods: {
      cancel () {
        this.$emit('show', false)
      },
      submit () {
        if (!this.params.name) {
          createTag(this.form)
        } else {
          editTag(this.form.id, this.form)
        }
        this.$emit('show', false)
      }
    },
    props: ['params']
  }
</script>



