import fetch from '@/config/fetch'

export const getTagsList = (page, pageSize) => fetch('/staff/producttype/?page=' + page + '&pageSize=' + pageSize, {}, 'GET')
export const createTag = (data) => fetch('/staff/producttype/', {data}, 'POST')
export const editTag = (id, data) => fetch('/staff/producttype/' + id + '/', {data}, 'PUT')
export const deleteTag = (id) => fetch('/staff/producttype/' + id, {}, 'DELETE')



