import fetch from '@/config/fetch'

export const getVideosList = (page, pageSize) => fetch('/staff/video/?page=' + page + '&pageSize=' + pageSize, {}, 'GET')
export const createVideo = (data) => fetch('/staff/video/', {data}, 'POST')
export const editVideo = (id, data) => fetch('/staff/video/' + id + '/', {data}, 'PUT')
export const deleteVideo = (id) => fetch('/staff/video/' + id, {}, 'DELETE')
export const getToken = () => fetch('/qiniutoken/', {}, 'GET')



