<style scoped lang="scss">
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .el-form {
    width: 80%;
    margin-top: 50px;
    margin-left: 50px;
  }
</style>

<template>
  <el-dialog
    :visible="is_show"
    title="视频上传"
    width="60%"
    top="25vh"
    center>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="视频名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="视频封面">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z2.qiniup.com"
          :show-file-list="false"
          list-type="picture"
          :data="QiniuData"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="form.image" :src="form.image" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="视频">
        <el-upload
          class=""
          action="https://upload-z2.qiniup.com"
          accept='.mp4,.qlv,.qsv,.ogg,.flv,.avi,.wmv,.rmvb'
          :data="QiniuData"
          :show-file-list="false"
          :before-upload="beforeUploadVideo"
          :on-success="handleVideoSuccess"
          :on-progress="uploadVideoProcess">
          <video
            v-if="form.uri !='' && videoFlag == false"
            :src="form.uri"
            width="350"
            height="180"
            controls="controls"
          >
            您的浏览器不支持视频播放
          </video>
          <i
            v-else-if="form.uri =='' && videoFlag == false"
            class="el-icon-plus avatar-uploader-icon"
          ></i>
          <el-progress
            v-if="videoFlag == true"
            type="circle"
            :percentage="videoUploadPercent"
            style="margin-top:30px"
          ></el-progress>
        </el-upload>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="cancel()">取 消</el-button>
    <el-button type="primary" @click="submit()">确 定</el-button>
  </span>
  </el-dialog>
</template>

<script>
  import {createVideo, editVideo, getToken} from './service'

  export default {
    data () {
      return {
        form: {
          name: '',
          image: '',
          status: 1,
          uri: ''
        },
        is_show: true,
        videoFlag: false,
        videoUploadPercent: '0%',
        QiniuData: {
          key: '',
          token: ''
        }

      }
    },
    mounted () {
      this.getToken()
      if (this.params.name) {
        this.form = this.params
      }
    },
    methods: {
      cancel () {
        this.$emit('show', false)
      },
      submit () {
        if (!this.params.name) {
          createVideo(this.form)
        } else {
          editVideo(this.form.id, this.form)
        }
        this.$emit('show', false)
      },
      beforeUploadVideo (file) {          //视频上传之前判断他的大小
        const isLt10M = file.size / 1024 / 1024 < 2000
        if (!isLt10M) {
          this.$message.error('上传视频大小不能超过2000MB哦!')
          return false
        }
        this.QiniuData.key = `${file.name}`
      },
      uploadVideoProcess (event, file, fileList) {    //视频上传的时候获取上传进度传给进度条
        this.videoFlag = true
        this.videoUploadPercent = parseInt(file.percentage)
      },
      handleVideoSuccess (res, file) {           //视频上传成功之后返回视频地址
        this.videoFlag = false
        this.videoUploadPercent = 0
        this.form.uri = 'http://lhxq.top/' + res.key
      },
      async getToken () {
        let res = await getToken()
        this.QiniuData.token = res.data.token
      },
      handleAvatarSuccess (res, file) {
        this.$set(this.form, 'image', 'http://lhxq.top/' + res.key)
      },
      beforeAvatarUpload (file) {
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!')
        }
        this.QiniuData.key = `${file.name}`
        return isLt2M
      }
    },
    props: ['params']
  }
</script>



