import fetch from '@/config/fetch'

export const getTagsList = (page, pageSize) => fetch('/staff/tag/?page=' + page + '&pageSize=' + pageSize, {}, 'GET')
export const createTag = (data) => fetch('/staff/tag/', {data}, 'POST')
export const editTag = (id, data) => fetch('/staff/tag/' + id + '/', {data}, 'PUT')
export const deleteTag = (id) => fetch('/staff/tag/' + id, {}, 'DELETE')



