<template>
  <el-dialog
    :visible="is_show"
    title="创建标签"
    width="50%"
    top="25vh"
    center>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="新增标签">
        <el-select v-model="form.tag_id" placeholder="请选择">
          <el-option
            v-for="item in List"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
        <el-button type="primary" @click="add()">新增标签</el-button>
      </el-form-item>
      <el-form-item label="删除标签">
        <el-select v-model="form.tag_id_delete" placeholder="请选择">
          <el-option
            v-for="item in List"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
        <el-button type="danger" @click="deletetag()">删除标签</el-button>
      </el-form-item>

      <el-form-item label="已有标签">
        <span v-for="item in form.tags" class="good-img">{{item.name}}、</span>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="cancel()">关闭</el-button>
  </span>
  </el-dialog>
</template>

<script>
  import {addTag, getTagsList, deleteTag} from '../service'
  export default {
    data () {
      return {
        form: {},
        is_show: true,
        List: []
      }
    },
    mounted () {
      this.getTagsList_()
      if (this.params.name) {
        this.form = this.params
      }
    },
    methods: {
      cancel () {
        this.$emit('show', false)
      },
      async getTagsList_ () {
        let res = await getTagsList(1, 10)
        this.List = res.data.results
      },
      submit () {
        this.$emit('show', false)
      },
      add () {
        let data = {
          tag_id: this.form.tag_id
        }
        addTag(this.form.id, data).then((res) => {
          if (res) {
            this.$message.success(`新增标签成功`)
          } else {
            this.$message.error(`新增标签失败`)
          }
        })
      },
      deletetag () {
        let data = {
          tag_id: this.form.tag_id_delete
        }
        deleteTag(this.form.id, data).then((res) => {
          if (res) {
            this.$message.success(`删除标签成功`)
          } else {
            this.$message.error(`删除标签失败`)
          }
        })
      }
    },
    props: ['params']
  }
</script>



