<style scoped>
  .good-img {
    margin-right: 20px;
  }
</style>
<template>
  <div class="wrapper-view">
    <div>
      <el-button type="primary" @click="showCreate = true">创建标签
      </el-button>
      <div style="min-height: 75vh">
        <el-table
          :data="List">
          <el-table-column
            prop="name"
            label="标签名称"
            width="180">
          </el-table-column>
          <el-table-column
            prop="create_at"
            label="创建时间"
            :formatter="this.globalMethod.dateFormat">
          </el-table-column>
          <el-table-column
            label="操作" width="300">
            <template slot-scope="scope">
              <el-button type="primary" size="small" @click="edit(scope.row)">编辑</el-button>
              <el-button type="danger" size="small" @click="deleteEntity(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-size="pagination.pageSize"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total">
      </el-pagination>
    </div>
    <create-tag v-if="showCreate" @show="showFunc" :params="params"></create-tag>
  </div>
</template>

<script>
  import {getTagsList, deleteTag} from './service'
  import createTag from './create'

  export default {
    data () {
      return {
        List: [],
        pagination: {
          currentPage: 1,
          total: 0,
          pageSize: 10
        },
        params: {},
        showCreate: false
      }
    },
    created () {
      // this.getList()
    },
    mounted () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    activated () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    methods: {
      async getList (page, pageSize) {
        let res = await getTagsList(page, pageSize)
        this.List = res.data.results
        this.pagination.total = res.data.count
      },
      async deleteTag_ (id) {
        let res = await deleteTag(id)
        if (res) {
          this.getList(this.pagination.currentPage, this.pagination.pageSize)
        }
      },
      handleSizeChange (val) {
        this.getList(this.pagination.currentPage, val)
      },
      handleCurrentChange (val) {
        this.getList(val, this.pagination.pageSize)
      },
      showFunc () {
        this.getList(this.pagination.currentPage, this.pagination.pageSize)
        this.showCreate = false
        this.params = {}
      },
      deleteEntity (id) {
        this.$confirm('确认删除？')
          .then(_ => {
            this.deleteTag_(id)
          })
          .catch(_ => {
          })
      },
      edit (val) {
        this.showCreate = true
        this.params = val
      }
    },
    components: {createTag}
  }
</script>



